const factorialCache: Map<string, string[][]> = new Map();

/**
 * Return row of factorial from 0 to n.
 * @customfunction FACTORIALROW
 * @param {number} n
 * @returns {Promise<string[][]>}
 */
export async function factorialRow(n: number): Promise<string[][]> {
    console.log("Enter value:", n);

    if (n < 0 || !Number.isInteger(n)) {
        throw new Error("Input must be a non-negative integer.");
    }
    else if (n > 500) {
        throw new Error("Input must be less then 500.");
    }

    const lOrientationMode: string | null = localStorage.getItem("orientationMode");
    // Create key from n and mode
    const lCacheKey = `${n}_${lOrientationMode}`;
    let lResult: string[][];

    console.log("OrientationMode in factorialRow from storage:", lOrientationMode);

    // Return calculated result from cache
    if (factorialCache.has(lCacheKey)) {
        lResult = factorialCache.get(lCacheKey)!;
        console.log("Result from factorialCache:", lResult);
    }
    else {
        const lResultArray: string[] = [];
        let factorial: bigint = BigInt(1);

        for (let i = 1; i <= n; i++) {
            factorial *= BigInt(i);
            lResultArray.push(factorial.toString());
        }

        if (lOrientationMode === "row") {
            lResult = [lResultArray];
        }
        else {
            lResult = lResultArray.map(val => [val]);
        }

        factorialCache.set(lCacheKey, lResult);

        console.log("Calculated result:", lResult);
    }

    return lResult;
}
CustomFunctions.associate("FACTORIALROW", factorialRow);